import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useIsMutating } from "@tanstack/react-query";
import { Button, FormGroup, Input } from "mtforms";
import { errorAlert, successAlert } from "../../../utils";
import { PublicPaths } from "../../../routes/path";
import { useForgetPassword } from "./hooks";

const ForgotPassword = () => {
	const navigate = useNavigate();
	const [formData, setFormData] = useState({});
	const [errors, setErrors] = useState({});
	const {
		mutate,
		isError,
		isSuccess,
		error: forgetPasswordError,
		reset,
	} = useForgetPassword();
	const isLoading = useIsMutating();

	const handleChange = (name, value) => {
		setFormData({ ...formData, [name]: value });
	};

	const validationHandler = (name, error) => {
		setErrors({ ...errors, [name]: error });
	};

	const submitHandler = () => {
		mutate(formData);
	};

	useEffect(() => {
		if (isSuccess) {
			reset();
			successAlert(
				"Password Reset Successfully. Check your mail for reset token"
			);
			navigate(PublicPaths.HOME);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [isSuccess]);

	if (isError) {
		reset();
		errorAlert(forgetPasswordError);
	}

	return (
		<>
			<h1>Forget Password</h1>
			<FormGroup onSubmit={submitHandler}>
				<Input
					name="email"
					label="Email Address"
					value={formData["email"]}
					onChange={handleChange}
					type="email"
					validationHandler={validationHandler}
					error={errors.email}
					required={true}
					size="large"
				/>

				<Button
					title="submit"
					size="large"
					className={isLoading ? "btn-disabled" : ""}
					loading={isLoading === 1}
					disabled={isLoading === 1}
				/>
			</FormGroup>
		</>
	);
};

export default ForgotPassword;
