import { axiosInstance } from "../../../../axios-Instance";
import { useMutation } from "@tanstack/react-query";

async function forgetPassword(formData) {
	const data = await axiosInstance({
		url: `/auth/forgot-password?email=${formData.email}`,
		method: "GET",
		headers: {
			"Content-Type": "application/json",
		},
	});

	return data?.data;
}

export function useForgetPassword() {
	const { mutate, isError, error, isSuccess, reset } = useMutation({
		mutationFn: (formData) => forgetPassword(formData),
	});
	return { mutate, isError, error, isSuccess, reset };
}
